import Vue from 'vue'
import Router from 'vue-router'
import Senior from './../components/Senior/Senior'
import junior from './../components/junior/junior'
import primary from './../components/primary/primary'
import common from './../components/common/common'
import study from './../components/study/study'
import hot from './../components/hot/hot'
import allSee from './../components/allSee/allSee'
import news from './../components/news/news'
import newslistDetail from './../components/newslistDetail/newslistDetail'
import {delCookie,getCookie,setCookie} from './../util/util'
import store from './../store/store'

Vue.use(Router)

const router =  new Router({
  routes: [
    {
      // +++++++++++++++++++++ 
      path: '/',
      redirect: '/index'
    },
    {
      //     
      path: '/index',
      name:'index',
      component: common,
      //  router
      children: [{
        // Senior   
        path: '',
        name:'Senior',
        component: Senior
      },
      {
        // junior   
        path: '/index/junior',
        name:'junior',
        component: junior
      },
      {
        // primary   
        path: '/index/primary/primary',
        name:'primary',
        component: primary
      }]
    },
    {
      // 
      path: '/study',
      name:'study',
      component: study
    },
    {
      // 
      path: '',
      component: hot,
      //  
      //  router
      children: [{
        // allSee   
        path: '/hot',
        name:'hot',
        component: allSee
      },
      {
        // news   
        path: '/hot/news',
        name:'news',
        component: news
      }]
    },
    {
      // 
      path: '/newslistDetail',
      name:'newslistDetail',
      component: newslistDetail
    }
  ]
})



// 
router.beforeEach((to, from, next) => {
  // to: Route:  
  // from: Route: 
  // next: Function:  resolve  next 

  //cookiecookieencode()decodeURI('string')cookieIOS
  console.log(getCookie('username'))
  const nextRoute = ['newslistDetail'];
  if(getCookie('username') == null){
    if(nextRoute.indexOf(to.name) > -1){
      console.log('');
      store.dispatch({
        type: 'showUser'
      })
      next(false);
      // router.push({path:'/login'})
    }else{
      console.log('')
      next();
    }
  }
  // router.push({name:'hot'});

  // const nextRoute = ['home', 'good-list', 'good-detail', 'cart', 'profile'];
  // let isLogin = global.isLogin;  // 
  // // nextRoutelogin
  // if (nextRoute.indexOf(to.name) >= 0) {  
  //   if (!isLogin) {
  //     console.log('what fuck');
  //     router.push({ name: 'login' })
  //   }
  // }
  // // loginhome 
  // if (to.name === 'login') {
  //   if (isLogin) {
  //     router.push({ name: 'home' });
  //   }
  // }
});

export default router;




